form#add-product {
  width: 100%;
  max-width: 580px;
  margin: 30px auto 0;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

form#add-product input {
  margin-bottom: 15px;
  height: 50px;
  border-radius: 4px;
  border: 1px solid #ddd;
  padding: 0 20px;
  font-size: 14px;
  font-size: 15px;
}

form#add-product button {
  font-size: 15px;
  padding: 20px 20px;
  border-radius: 4px;
  border: 0;
  background: #7159c1;
  color: #FFF;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
}